/* eslint-disable @typescript-eslint/ban-types */
import {Component, ChangeDetectionStrategy, ClassProvider, Optional, Inject, PLATFORM_ID} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {DOCUMENT} from '@angular/common';
import {GlobalNotificationsService} from '@anglr/notifications';
import {ComponentRoute} from '@anglr/common/router';
import {AuthGuard, Authorize} from '@anglr/authentication';
import {BaseHelpComponent, HelpService} from '@anglr/md-help/web';
import {NormalizationCharMaps} from '@jscrpt/common';

import {HelpService as HelpServiceImpl} from '../../../services/api/help';

/**
 * Page used for displaying help pages
 */
@Component(
{
    selector: 'help-view',
    template: '<div #content></div>',
    styles: [':host{overflow-y: scroll;}'],
    providers: 
    [
        <ClassProvider>
        {
            provide: HelpService,
            useClass: HelpServiceImpl
        }
    ],
    changeDetection: ChangeDetectionStrategy.OnPush
})
@ComponentRoute({path: '**', canActivate: [AuthGuard]})
@Authorize('help-page')
export class HelpComponent extends BaseHelpComponent
{
    //######################### protected fields #########################

    /**
     * Base url for md
     */
    protected _baseUrl: string = '/pomoc';

    /**
     * Charmap used for normalization
     */
    protected _charMap: Object = NormalizationCharMaps.slovak;

    //######################### constructor #########################
    constructor(route: ActivatedRoute,
                helpSvc: HelpService,
                router: Router,
                @Optional() notifications: GlobalNotificationsService,
                @Inject(DOCUMENT) document: HTMLDocument,
                @Inject(PLATFORM_ID) platformId: Object)
    {
        super(route, helpSvc, router, notifications, document, platformId);
    }

    /**
     * Redirects to not found page
     */
    protected _showNotFound()
    {
        this._router.navigate(['/notFound']);
    }
}